<template>
  <div class="qisme-container">
    <div class="sidebar">
      <div class="sidebar__header">
        <strong>{{ qiscus.username }}</strong>
        <v-spacer></v-spacer>
        <div class="sidebar__icons">
          <v-icon dark v-if="sidebarMode != 'contact'" @click="sidebarMode='contact'" >people</v-icon>
          <v-icon dark v-if="sidebarMode == 'contact'"  @click="sidebarMode='conversation'">arrow_back</v-icon>
          <v-menu bottom right>
            <v-btn icon slot="activator" dark>
              <v-icon>more_vert</v-icon>
            </v-btn>
            <v-list>
              <v-list-tile>
                <v-list-tile-title @click="showGroupModal"><v-icon>group_add</v-icon> Create Group</v-list-tile-title>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-title @click="logout"><v-icon>power_settings_new</v-icon> Logout</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </div>
      </div>
      <div class="sidebar__content">
        <div class="room-loader" v-if="!conversationIsLoaded">
          <div class="loader"></div>
          <p>Loading Conversations</p>
        </div>
        <v-slide-y-transition mode="out-in">
          <conversation-list
            :conversations="conversations"
            v-if="sidebarMode === 'conversation' && conversationIsLoaded"
          ></conversation-list>
          <contact-list
            :contacts="contacts"
            v-if="sidebarMode === 'contact' && conversationIsLoaded"
          ></contact-list>
        </v-slide-y-transition>
      </div>
      <div class="sidebar__footer">
        Version {{version_number}}
      </div>
    </div>
    <div class="widget-container">
      <div class="widget-wrapper" :class="{ hidden: !qiscus.selected }">
        <div id="qiscus-widget"></div>
      </div>
      <div class="splash-screen" :class="{ hidden: qiscus.selected }" style="text-align: center;">
        <div class="img-placeholder medium"></div>
        <p>Please select a conversation to start messaging</p>
      </div>
      <v-slide-y-transition>
        <div class="conversation-detail" v-if="isActiveDetail">
          <v-layout column>
            <v-flex class="conversation-detail__header" xs12 pa-3>
              <v-icon light @click="toggleConversationDetail">clear</v-icon> Contact Detail
            </v-flex>
            <v-flex xs12 class="conversation-detail__info">
              <ul>
                <li>
                  <img :src="qiscus.selected.avatar">
                </li>
                <li>{{ qiscus.selected.name }}</li>
                <li>
                  <small>Type</small>
                  <strong>{{ qiscus.selected.room_type }}</strong>
                </li>
                <li>
                  <small>Members</small>
                  <strong>{{ qiscus.selected.participants.length }}</strong>
                </li>
              </ul>
            </v-flex>
          </v-layout>
        </div>
      </v-slide-y-transition>
    </div>
    <!-- create Group modal -->
    <v-dialog v-model="createGroupDialog" scrollable width="500">
      <v-card>
        <v-card-title><input type="text" placeholder="Group Name" v-model="createGroupName"></v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;">
          <v-list three-line class="addmember__contactlist">
            <v-list-tile avatar v-for="contact in contacts" :key="contact.id">
              <v-list-tile-avatar>
                <img v-bind:src="contact.avatar || 'https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mm&f=y'"></v-list-tile-avatar>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ contact.username}}</v-list-tile-title>
                <v-list-tile-sub-title>{{ contact.email }}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-checkbox :value="contact.email" v-model="createGroupEmails"></v-checkbox>
              </v-list-tile-action>
            </v-list-tile>
          </v-list>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn class="blue--text darken-1" flat @click.native="closeGroupModal">Close</v-btn>
          <v-btn class="blue--text darken-1" flat @click.native="createGroup">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- add member modal -->
    <v-dialog v-model="addMemberDialog" scrollable width="500">
      <v-card>
        <v-card-title>Add Member</v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;">
          <v-list three-line class="addmember__contactlist">
            <v-list-tile avatar v-for="contact in contacts" :key="contact.id">
              <v-list-tile-avatar>
                <img v-bind:src="contact.avatar || 'https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mm&f=y'"></v-list-tile-avatar>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ contact.username }}</v-list-tile-title>
                <v-list-tile-sub-title>{{ contact.email }}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-checkbox :value="contact.email" v-model="createGroupEmails"></v-checkbox>
              </v-list-tile-action>
            </v-list-tile>
          </v-list>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn class="blue--text darken-1" flat @click.native="closeAddMemberModal">Close</v-btn>
          <v-btn class="blue--text darken-1" flat @click.native="addMember">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- room info modal -->
    <room-info :status="roominfoDialog" :roominfo="roominfo" :addmemberButtonHandler="showAddMemberModal" :dismissHandler="closeRoomInfoDialog"></room-info>
    <sandbox-widget :target="qiscus.email"></sandbox-widget>
  </div>
</template>

<script>
import localstore from 'store';
import ConversationList from './ConversationList';
import ContactList from './ContactList';
import RoomInfo from './RoomInfo';
import SandboxWidget from './Sandbox/SandboxWidget'
import * as Utils from './Sandbox/utils'
import packageJson from '../../package.json'


export default {
  name: 'Qisme',
  components: { ConversationList, ContactList, SandboxWidget, RoomInfo },
  computed: {
    version_number() { return packageJson.version },
    conversations() { return this.filterList(this.$store.state.conversations); },
    contacts() { return this.filterList(this.$store.state.contacts); },
    isActiveDetail() { return this.$store.state.isActiveDetail; },
    qiscus() { return qiscus; },
    roominfo() { return this.$store.state.roominfo; },
    isLoadingConversations() { return this.$store.state.conversations.length < 1; },
    notificationList() { return this.$store.state.notificationList; },
    notificationCount() { return this.$store.state.notificationCount; },
    filterPlaceholder() { return (this.sidebarMode === 'conversation') ? 'Search Conversation' : 'Search Contact'; },
  },
  data() {
    return {
      filterText: '',
      title: 'Qiscus SDK Sandbox',
      sidebarMode: 'conversation', // ['conversation','contact']
      createGroupDialog: false,
      createGroupEmails: [],
      createGroupName: '',
      addMemberDialog: false,
      roominfoDialog: false,
      conversationIsLoaded: false,
    };
  },
  beforeCreate() {
    // check the localstorage
    this.$store.dispatch('setAuthData');
    this.$store.dispatch('getContacts');
    // if (!localstore.get('qisme')) return this.$router.push('/');
    return true;
  },
  created() {
    // get conversation lists
    this.$store.dispatch('loadConversations')
    .then((response) => {
      this.conversationIsLoaded = true;
    });
    // this.$store.dispatch('loadContacts');
  },
  mounted() {
    /* eslint-disable */
    const self = this;
    const dataFromStore = localstore.get('qisme');
    qiscus.init({
      AppId: __APP_ID__,
      mode: 'wide',
      options: {
        groupRoomCreatedCallback ( data ) {
          self.$store.dispatch('loadConversations');
          qiscus.UI.chatGroup(data.id);
        },
        headerClickedCallback(data) {
          console.info(data);
          self.getRoomInfo();
        },
        newMessagesCallback(data) {
          console.info('new messages', data)
          self.$store.dispatch('loadConversations');
        },
      }
    });

    const params = Utils.parseQueryString(decodeURIComponent(location.search));
    let email, password, username;
    password = 'password'

    if (params.hasOwnProperty('username')) {
      email = params.email
      username = params.username
    } else {
      email = 'customer-service@email.com'
      username = 'Customer Service'
    }

    qiscus.setUser(email, password, username)
    // qiscus.setUser(dataFromStore.data.qiscus_email, 'password', dataFromStore.data.fullname || dataFromStore.data.qiscus_email);
    QiscusSDK.render();
    /* eslint-enable */
  },
  methods: {
    getRoomInfo() {
      this.$store.dispatch('getRoomInfo').then(() => this.roominfoDialog = true);
    },
    closeRoomInfoDialog() {
      this.roominfoDialog = false;
    },
    showGroupModal() {
      this.createGroupDialog = true;
    },
    closeGroupModal() {
      this.createGroupDialog = false;
    },
    showAddMemberModal() {
      this.addMemberDialog = true;
    },
    closeAddMemberModal() {
      this.addMemberDialog = false;
    },
    createGroup() {
      const emails = this.createGroupEmails;
      emails.push("customer-service@email.com");
      qiscus.createGroupRoom(this.createGroupName, emails)
      .then((response) => {
        this.createGroupName = '';
        this.createGroupEmails.length = 0;
        this.createGroupDialog = false;
        // qiscus.UI.chatGroup();
      });
    },
    addMember() {
      const emails = this.createGroupEmails;
      this.$store.dispatch('addMember', emails)
      .then((response) => {
        alert('Successfully add member');
        this.createGroupEmails.length = 0;
        this.addMemberDialog = false;
      }, (error) => {
        console.info(error);
        this.createGroupDialog = false;
        alert('Failed adding member');
      });
    },
    logout() {
      localstore.clearAll();
      this.$router.push('/');
    },
    toggleConversationDetail() {
      this.$store.dispatch('toggleConversationDetail');
    },
    filterList(data) {
      const filterText = this.filterText;
      const getIndex = function getIndex(text) {
        return text.toLowerCase().indexOf(filterText) > -1;
      };
      if (filterText && this.sidebarMode === 'conversation') {
        return data
          .filter(conv => getIndex(conv.chat_name) || getIndex(conv.last_message));
      } else if (filterText && this.sidebarMode === 'contact') {
        return data
          .filter(contact => getIndex(contact.fullname));
      }
      return data;
    },
  },
};
</script>



