<template>
  <div class="login-container">
    <v-container fluid transition="v-slide-y-transition">
      <v-snackbar
        v-model="snackbar"
        :success="snackbarSuccess"
        :error="snackbarError"
      >
        {{ snackbarText }}
        <v-btn flat class="pink--text" @click.native="snackbar = false">Close</v-btn>
      </v-snackbar>
      <v-layout>
        <v-flex sm6>
          <h1 @click="verificationIsSent = !verificationIsSent">Welcome</h1>

          <!-- Step 1 -->
          <form @submit.prevent="sendVerification" v-if="!verificationIsSent">
            <p>Enter phone number to Login</p>
            <v-layout row>
              <v-flex xs4>
                <v-select
                  v-bind:items="countryCodes"
                  v-model="countryCode"
                  item-text="dial_code"
                  item-value="dial_code"
                  label="Select"
                  dark
                  single-line
                  auto
                >
                  <template slot="item" scope="data">
                    {{ data.item.name }} ({{ data.item.code }})
                  </template>
                </v-select>
              </v-flex>
              <v-flex xs8>
                <v-text-field
                  name="phone"
                  label="Phone"
                  v-model="phone"
                  id="testing"
                ></v-text-field>
              </v-flex>
            </v-layout>

            <button :class="{'inactive': phone.length < 8}">Continue</button>
          </form>

          <!-- Step 2 -->
          <v-layout v-if="verificationIsSent" column>
            <v-flex sm12>
              <h2 class="subheading">Enter your Code</h2>
              <p>a text message was sent to {{ countryCode + phone }}</p>
              <v-btn flat class="btn--dark-flat-focused" @click.native="verificationIsSent = false">Change Number</v-btn>
            </v-flex>
            <v-flex sm12 class="elevation-0">
              <form @submit.prevent="verify">
                <v-text-field
                    name="passCode"
                    label="Input your Code here"
                    v-model="passCode"
                    id="passCode"
                  ></v-text-field>
                  <v-btn dark
                    class="btn--dark-flat-focused"
                    @click.native="resendVerification"
                    v-if="passCode.length < 4"
                  >Resend Verification</v-btn>
                  <v-btn success
                    @click.native="verify"
                    v-if="passCode.length >= 4"
                    class="teal white--text"
                  >Sign In</v-btn>
              </form>
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex sm6>
          <!-- <img src="../assets/emoji-phone.png" alt="Qisme Login Illustration"> -->
          <img class="img-placeholder medium">
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios';
import localstore from 'store';
import countryCodes from '../helpers/country.code';

export default {
  name: 'Login',
  data() {
    return {
      countryCode: '+62',
      countryCodes,
      phone: '',
      verificationIsSent: false,
      passCode: '',
      snackbar: false,
      snackbarError: false,
      snackbarSuccess: false,
      snackbarText: '',
    };
  },
  beforeCreate() {
    // check the localstorage
    const authData = localstore.get('qisme');
    if (authData && 'access_token' in authData) return this.$router.push('app');
    return true;
  },
  methods: {
    getCountryCode(dialCode) {
      this.countryCode = dialCode;
    },
    sendVerification() {
      if (this.phone.length < 1) {
        this.setErrorMessage('Please fill all the required field');
        return;
      }
      const data = {
        user: {
          app_id: __APP_ID__,
          phone_number: this.countryCode + this.phone,
        },
      };
      axios.post(`${__API__}auth`, data)
      .then(() => {
        this.verificationIsSent = true;
      }, () => this.setErrorMessage('Failed getting verification code'));
    },
    resendVerification() {
      const self = this;
      const data = {
        user: {
          app_id: __APP_ID__,
          phone_number: self.countryCode + self.phone,
        },
      };
      axios.post(`${__API__}auth/resend_passcode`, data)
      .then(() => self.setSuccessMessage('Verification Code re-Sent'))
      .catch(() => self.setErrorMessage('Failed re-sending verification code'));
    },
    verify() {
      const data = {
        user: {
          app_id: __APP_ID__,
          phone_number: this.countryCode + this.phone,
          passcode: this.passCode,
        },
      };
      axios.post(`${__API__}auth/verify`, data)
      .then((response) => {
        localstore.set('qisme', response.data);
        this.$store.dispatch('setAuthData');
        this.$router.push('app');
      }, () => this.setErrorMessage('Failed Signing In'));
    },
    setErrorMessage(message) {
      this.snackbar = true;
      this.snackbarText = message;
      this.snackbarSuccess = false;
      this.snackbarError = true;
    },
    setSuccessMessage(message) {
      this.snackbar = true;
      this.snackbarText = message;
      this.snackbarSuccess = true;
      this.snackbarError = false;
    },
  },
};
</script>



