import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/Login';
import Qisme from '@/components/Qisme';
import Widget from '@/components/Sandbox/Widget';
import Sandbox from '@/components/Sandbox/Sandbox';
import Seeder from '@/components/Seeder';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/app'
    },
    // {
    //   path: '/',
    //   name: 'Login',
    //   component: Login,
    // },
    {
      path: '/app',
      name: 'MainApp',
      component: Qisme,
    },
    {
      path: '/seed',
      name: 'Seed',
      component: Seeder,
    },
    {
      path: '/widget',
      name: 'WidgetMode',
      component: Widget,
    },
    {
      path: '/sandbox',
      name: 'SandboxMode',
      component: Sandbox
    }
  ],
});



