
<template>
<v-list two-line>
  <template v-for="item in guests">
    <li>
      <v-list-tile
        avatar
        v-model="item.active"
        v-bind:key="item.id"
        v-on:click.native="onChat(item)">
        <v-list-tile-avatar>
          <img v-bind:src="item.avatar">
        </v-list-tile-avatar>
        <v-list-tile-content>
          <v-list-tile-title v-html="item.username"></v-list-tile-title>
          <v-list-tile-sub-title v-html="item.email"></v-list-tile-sub-title>
        </v-list-tile-content>
      </v-list-tile>
    </li>
  </template>
</v-list>
</template>

<style lang="sass" scoped>
  a.list__tile.list__tile--active.list__tile--avatar
    background: lightgrey
</style>

<script>
export default {
  name: 'GeustList',
  props: ['guests'],
  methods: {
    onChat: function (guest) {
      this.$emit('chat-target', guest)
    }
  }
}
</script>



