<template>
  <v-layout column>
    <v-flex sm12>
      <v-list two-line>
        <v-list-tile avatar v-for="contact in contacts" v-bind:key="contact.id">
          <v-list-tile-avatar>
            <img v-bind:src="contact.avatar_url || 'https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mm&f=y'">
          </v-list-tile-avatar>
          <v-list-tile-content @click="chatTarget(contact)">
            <v-list-tile-title>{{ contact.username }}</v-list-tile-title>
            <v-list-tile-sub-title>{{ contact.email }}</v-list-tile-sub-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'contactList',
  props: ['contacts'],
  data() {
    return {
      officialDialog: false,
      officialActive: false,
      contactActive: true,
    };
  },
  methods: {
    chatTarget(contact) {
      const self = this;
      qiscus.UI.chatTarget(contact.email);
    },
  },
};
</script>



