<template>
  <div :class="{'sandbox-widget-container': true, hidden: widgetHidden}">
    <div class="close-btn-container">
      <v-btn icon light @click.native="toggleWidget">
        <v-icon v-if="widgetHidden">chevron_left</v-icon>
        <v-icon v-else>chevron_right</v-icon>
      </v-btn>
    </div>
    <iframe :src="iframeSource"></iframe>
  </div>
</template>
<style scoped lang="sass">
.sandbox-widget-container
  z-index: 1000
  position: fixed
  top: 1em
  right: 0
  height: 45rem
  width: 25rem
  box-shadow: 7px 5px 10px rgba(0,0,0,.3)
  padding: 10px
  background: grey
  border-radius: 10px 0 0 10px
  transition: right ease 0.2s
  &.hidden
    right: -350px
    display: block
.close-btn-container
  position: absolute
  top: 10px
  left: -40px
  background: grey
  border-radius: 10px 0 0 10px
iframe
  border: none
  height: 100%
  width: 100%
</style>
<script>
export default {
  name: 'QiscusSandboxWidget',
  props: ['target'],
  data: () => ({
    widgetHidden: true 
  }),
  computed: {
    iframeSource() { return `/widget?target=${this.$props.target}` }
  },
  methods: {
    toggleWidget() {
      this.widgetHidden = !this.widgetHidden
    }
  }
}
</script>



