<template>
  <div>
    <v-alert success value="true" v-if="dataIsSeeded">
        Data has been seeded.
    </v-alert>
  </div>
</template>

<script>
export default {
  name: 'Seeder',
  data() {
    return {
      dataIsSeeded: false,
    }
  },
  created() {
    this.$store.dispatch('seedData').then(() => {
      this.dataIsSeeded = true;
    });
  },
}
</script>




