<template>
  <v-dialog v-model="status" width="650" persistent>
      <v-card>
        <v-card-title class="headline">
          {{ roominfo.room_name }}
          <v-spacer></v-spacer>
          <v-btn icon @click.stop="addmemberButtonHandler" v-if="roominfo.room_type == 'group'">
            <v-icon>group_add</v-icon>
          </v-btn>
          <v-btn icon @click="dismissHandler">
            <v-icon>close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>Last comment message was `{{ roominfo.last_comment_message }}` on {{ roominfo.last_comment_timestamp }}</v-card-text>
        <v-list three-line class="addmember__contactlist">
            <v-list-tile avatar v-for="user in roominfo.participants" :key="user.email">
              <v-list-tile-avatar>
                <img v-bind:src="user.avatar_url || 'https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mm&f=y'"></v-list-tile-avatar>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ user.username }}</v-list-tile-title>
                <v-list-tile-sub-title>{{ user.email }}</v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-btn icon @click="deleteParticipant(user)">
                  <v-icon>delete</v-icon>
                </v-btn>
              </v-list-tile-action>
            </v-list-tile>
          </v-list>
      </v-card>
    </v-dialog>
</template>

<script>
export default {
  name: 'RoomInfo',
  props: ['status','roominfo','addmemberButtonHandler', 'dismissHandler'],
  data() {
    return {
      users: []
    }
  },
  methods: {
    deleteParticipant(participant) {
      this.$store.dispatch('deleteMember', participant)
      .then(response => {
        alert('user deleted');
        this.dismissHandler();
      });
    }
  }
}
</script>


