<template>
  <v-layout column>
    <v-flex sm12>
      <v-list two-line>
        <v-list-tile avatar v-for="conversation in conversations" 
          v-bind:key="conversation.id"
          :class="{ 'list__tile--active': activeChat == conversation.id }">
          <v-list-tile-avatar>
            <img v-bind:src="conversation.room_avatar_url || 'https://www.gravatar.com/avatar/00000000000000000000000000000000?d=mm&f=y'">
          </v-list-tile-avatar>
          <v-list-tile-content @click="chatTarget(conversation)">
            <v-list-tile-title v-html="conversation.room_name"></v-list-tile-title>
            <v-list-tile-sub-title>{{ conversation.last_comment_message }}</v-list-tile-sub-title>
          </v-list-tile-content>
          <v-list-tile-action v-if="conversation.unread_count">
            <v-chip class="green white--text">
              {{ conversation.unread_count }}
            </v-chip>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'ConversationList',
  props: ['conversations'],
  computed: {
    activeChat() { return this.$store.state.activeChat; },
  },
  methods: {
    chatTarget(conversation) {
      this.$store.dispatch('chatTarget', conversation)
      .then(() => this.$store.dispatch('loadConversations'));
    },
    chatGroup(id) {
      qiscus.UI.chatGroup(id)
      .then(() => this.$store.dispatch('loadConversations'));
    }
  },
};
</script>



