export const getGuests = () => Array.from(new Array(10).keys())
  .map(function (index) {
    return {
      id: index,
      email: `guest${index+1}@gg.com`,
      password: 'password',
      username: `Guest ${index+1}`,
      avatar: 'https://res.cloudinary.com/qiscus/raw/upload/v1493375329/1493375328/avatar%403x.png'
    }
  })


const conversations = Array.from(new Array(10).keys())
  .map(it => it + 1)
  .map(function (index) {
    return {
      id: index,
      qiscus_room_name: `Guest ${index}`,
      qiscus_room_id: 0x100 + index,
      is_group_chat: false,
      created_at: Date.now(),
      updated_at: Date.now(),
      group_avatar_url: '',
      is_official_chat: false,
      users: [
       {
          id: 0x010 + index,
          fullname: `Guest ${index}`,
          email: `guest${index}@gg.com`,
          qiscus_email: `guest${index}@gg.com`,
        }
      ],
      target: {},
      creator: {},
      chat_name: `Guest ${index}`,
      chat_avatar_url: 'https://res.cloudinary.com/qiscus/raw/upload/v1493375329/1493375328/avatar%403x.png',
      unread_count: 0,
      last_message: '',
      last_message_timestamps: Date.now(),
      group_avatar_url_sdk: ''
    }
  })
export const getConversations = () => Promise.resolve(conversations)

export const setFormData = (data) => {
  const formData = new FormData();
  Object.keys(data).forEach(key => formData.append(key, data[key]));
  return formData;
};

export const parseQueryString = (url) => {
  const urlParams = {};
  url.replace(
    new RegExp("([^?=&]+)(=([^&]*))?", "g"),
    function($0, $1, $2, $3) {
      urlParams[$1] = $3;
    }
  );
  
  return urlParams;
}


