import Vue from 'vue';
import Vuex from 'vuex';
// import localstore from 'store';
import axios from 'axios';
import * as Utils from '../components/Sandbox/utils'

Vue.use(Vuex);

const httpClient = axios.create({
  baseURL: __API__,
  // headers: { Authorization: `Token ${authData.access_token}` },
  headers: { 
    Qiscus_sdk_secret: '2820ae9dfc5362f7f3a10381fb89afc7',  
  //   'Content-Type' : 'application/x-www-form-urlencoded; charset=UTF-8',
  },
});

const actions = {
  loadConversations({ commit }) {
    // get conversation lists
    let email = 'customer-service@email.com';
    const params = Utils.parseQueryString(decodeURIComponent(location.search));
    if (params.email) email = params.email;
    return httpClient.get(`get_user_rooms?user_email=${email}`)
    .then((response) => {
      commit('LOAD_CONVERSATIONS', response.data.results.rooms_info);
    });
  },
  seedData({ commit }) {
    return Utils.getConversations()
    .then((data) => {
      const users = data.map(d => d.users[0]);
      commit('LOAD_CONVERSATIONS', data);
      return users;
    })
    .then((users) => {
      users.forEach((user) => {
        const url = `https://${__APP_ID__}.qiscus.com/api/v2/sdk/login_or_register`;
        const body = new FormData();
        body.append('email', user.email);
        body.append('username', user.fullname);
        body.append('password', 'password');
        fetch(url, {
          method: 'POST',
          body,
        });
      });
    });
  },
  getContacts({ commit }) {
    const guests = Utils.getGuests();
    commit('SET_CONTACTS', guests);
  },
  addMember({commit, state}, payload) {
    return httpClient.post('add_room_participants', {
      room_id: state.qiscus.selected.id,
      emails: payload,
    });
  },
  deleteMember({commit, state}, payload) {
    const data = Utils.setFormData({
      room_id: state.qiscus.selected.id,
      'emails[]': payload.email,
    });
    return httpClient.post('remove_room_participants', data)
  },
  getRoomInfo({ state, commit }) {
    const data = Utils.setFormData({
      user_email: state.qiscus.email,
      show_participants: true,
      'room_id[]': state.qiscus.selected.id,
    });
    httpClient.post('get_rooms_info', data).
    then(response => commit('SET_ROOM_INFO', response.data.results.rooms_info[0]));
  },
  loadContacts({ commit, state }) {
    // get conversation lists
    httpClient.get('contacts', {
      headers: { Authorization: `Token ${state.authData.access_token}` },
    })
    .then((response) => {
      commit('LOAD_CONTACTS', response.data.data);
    });
  },
  popNotificationList({ commit }) {
    setTimeout(() => commit('POP_NOTIFICATION_LIST'), 3000);
  },
  toggleConversationDetail({ commit }) {
    commit('TOGGLE_CONVERSATION_DETAIL');
  },
  setAuthData({ commit }) {
    const authData = {
      data: {
        qiscus_email: 'customer-service@email.com'
      }
    }
    commit('SET_AUTH_DATA', authData);
  },
  setOfficialDialogData({ commit }, payload) {
    commit('SET_OFFICIAL_DIALOG_DATA', payload);
  },
  createOfficialChatRoom({ commit, state }, payload) {
    return httpClient.post('chat/conversations/group_chat', {
      chat_name: payload.chat_name,
      qiscus_room_id: payload.qiscus_room_id,
      group_avatar_url: payload.group_avatar_url,
      is_official_chat: true,
      target_user_id: [payload.target_user_id],
    }, {
      headers: { Authorization: `Token ${state.authData.access_token}` },
    });
  },
  createConversation({ commit, state }, payload) {
    return httpClient.post('chat/conversations', {
      target_user_id: payload.id,
      qiscus_room_id: qiscus.selected.id,
    }, {
      headers: { Authorization: `Token ${state.authData.access_token}` },
    });
  },
  setUnreadCount({ commit, state }, id) {
    commit('SET_UNREAD_COUNT', id);
  },
  setActiveChat({ commit }, id) {
    commit('SET_ACTIVE_CHAT', id);
  },
  chatTarget({ commit, dispatch, state }, conversation) {
    qiscus.UI.chatGroup(conversation.room_id);
    commit('RESET_UNREAD_COUNT', conversation.room_id);
    commit('SET_ACTIVE_CHAT', conversation.room_id);
    commit('SUBTRACT_TITLE_UNREAD_COUNT', conversation.unread_count);
    commit('SET_NOTIFICATION_COUNT', 0);
    dispatch('resetDocumentTitle', conversation.unread_count);
  },
  resetDocumentTitle({ state }, payload) {
    state.notificationCount -= payload;
    if (state.notificationCount < 1) {
      state.notificationCount = 0;
      document.title = 'Qiscus SDK Sabndbox';
    } else {
      document.title = `(${state.notificationCount}) New Message(s)`;
    }
  },
};

const mutations = {
  SET_CONTACTS(state, payload) {
    state.contacts = payload;
  },
  LOAD_CONVERSATIONS(state, payload) {
    state.conversations = payload;
  },
  LOAD_CONTACTS(state, payload) {
    state.contacts = payload;
  },
  TOGGLE_CONVERSATION_DETAIL(state) {
    state.isActiveDetail = !state.isActiveDetail;
  },
  SET_AUTH_DATA(state, payload) {
    state.authData = payload;
  },
  SET_OFFICIAL_DIALOG_DATA(state, payload) {
    state.officialDialogData = Object.assign({}, payload);
  },
  POP_NOTIFICATION_LIST(state) {
    state.notificationList.pop();
  },
  RESET_UNREAD_COUNT(state, id) {
    state.conversations = state.conversations.map((value) => {
      if (value.id === id) {
        value.unread_count = 0;
      }
      return value;
    });
  },
  SET_NOTIFICATION_COUNT(state, count) {
    state.notificationCount = count;
  },
  SET_NOTIFICATION_LIST(state, notif) {
    state.notificationList.push(notif);
  },
  SET_ACTIVE_CHAT(state, id) {
    state.activeChat = id;
  },
  SET_ROOM_INFO(state, payload) {
    state.roominfo = payload;
  },
  SUBTRACT_TITLE_UNREAD_COUNT(state, count) {
    state.notificationCount -= count;
  },
};

const store = new Vuex.Store({
  state: {
    qiscus,
    conversations: [],
    contacts: [],
    isActiveDetail: false,
    authData: {},
    officialDialogData: {},
    activeChat: {},
    notificationList: [],
    notificationCount: 0,
    roominfo: {},
  },
  actions,
  mutations,
});
export default store;



