<template>
<div class="wrapper">
  <v-container fluid v-if="!isLoggedIn">
    <form v-on:submit.prevent="onSubmit">
      <v-flex xs2>
        <v-subheader>
          Login as
        </v-subheader>
      </v-flex>
      <v-flex xs8>
        <v-select
          v-bind:items="guests"
          v-model="selectedGuest"
          item-value="email"
          label="Login as"
          single-line>
        </v-select>
      </v-flex>
      <v-btn block
        light
        type="submit"
        class="blue-grey">
        Login
      </v-btn>
    </form>
  </v-container>
  <div v-bind:class="{'widget-container': true, hidden: !isLoggedIn}">
    <v-btn small
      outline
      @click.native="logout"
      class="blue-grey logout-btn">
      Logout
    </v-btn>
    <div id="qiscus-widget"></div>
  </div>
</div>
</template>

<style scoped lang="sass">
.wrapper,
.widget-container
  height: 100%
  width: 100%
.logout-btn
  position: absolute
  top: 7px
  right: 10px
  z-index: 100
</style>

<script>
export default {
  name: 'Widget',
  data() {
    return {
      isLoggedIn: false,
      guests: Array.from(new Array(10).keys())
        .map(function (index) {
          return {
            text: `Guest ${index+1}`,
            email: `guest${index+1}@gg.com`
          }
        }),
      selectedGuest: '',
    }
  },
  computed: {
    chatTarget() {
      const target = window.location.search
        .split('&')
        .map(it => it.split('='))
        .map(it => it[1])
        .pop()
        .replace('%40', '@')
      return target
    }
  },
  methods: {
    logout() {
      window.location.reload()
    },
    onInput(selectedGuest) {
      console.info('called');
      this.selectedGuest = selectedGuest
    },
    onSubmit(e) {
      const guest = this.guests.find(it => it.email === this.selectedGuest)
      qiscus.setUser(guest.email, 'password', guest.text)
      this.isLoggedIn = true
    }
  },
  mounted() {
    qiscus.init({
      AppId: __APP_ID__,
      mode: 'wide',
      options: {
        loginSuccessCallback: () => {
          this.isLoggedIn = true
          qiscus.UI.chatTarget(this.chatTarget)
        }
      }
    })
    QiscusSDK.render()
  }
}
</script>



