<template>
<v-app id='sandbox' standalone>
  <v-navigation-drawer
    permanent
    persistent
    clipped
    light>
    <guest-list
      v-bind:guests="guests"
      v-on:chat-target="onChatTarget">
    </guest-list>
  </v-navigation-drawer>
  <v-toolbar class="blue-grey lighten-1" dark>
    <v-toolbar-title>
      Toolbar
    </v-toolbar-title>
  </v-toolbar>
  <main>
    <div class="notice">
      <p>
        You are logged in as Customer Service. (cs@company.com) as an example
      </p>
    </div>
    <div class="qiscus-chat-container">
      <div id="qiscus-widget"></div>
    </div>
    <div v-bind:class="{'sandbox-widget-container': true, hidden: widgetHidden}">
      <div class="close-btn-container">
        <v-btn icon light v-on:click.native="toggleWidget">
          <v-icon v-if="widgetHidden">chevron_left</v-icon>
          <v-icon v-else>chevron_right</v-icon>
        </v-btn>
      </div>
      <iframe src="/widget"></iframe>
    </div>
  </main>
</v-app>
</template>

<style scoped lang="sass">
#sandbox.application
  height: 100%
  width: 100%
main
  height: calc(100% - 1rem)
.qiscus-chat-container
  position: absolute
  top: 0
  left: 300px
  right: 0
  bottom: 5em
.notice
  position: absolute
  top: 0
  right: 0
  background: rgba(0,0,0,.4)
  color: white
  padding: 5px 10px
  z-index: 1000
.sandbox-widget-container
  z-index: 1000
  position: absolute
  top: 10em
  right: 0
  height: 45rem
  width: 25rem
  box-shadow: 7px 5px 10px rgba(0,0,0,.3)
  padding: 10px
  background: grey
  border-radius: 10px 0 0 10px
  transition: right ease 0.2s
  &.hidden
    right: -350px
    display: block
.close-btn-container
  position: absolute
  top: 10px
  left: -40px
  background: grey
  border-radius: 10px 0 0 10px
iframe
  border: none
  height: 100%
  width: 100%
</style>

<script>
import GuestList from './GuestList.vue'
import QiscusChatContainer from './QiscusChatContainer.vue'
import Widget from './Widget'
import * as utils from './utils'

const guests = utils.getGuests()
  .map(guest => ({ ...guest, active: false }))

export default {
  name: 'Sandbox',
  components: {
    GuestList,
    QiscusChatContainer,
    Widget
  },
  data() {
    return {
      guests: utils.getGuests(),
      widgetHidden: false
    }
  },
  beforeMount() {
    qiscus.init({
      AppId: 'dragongo',
      mode: 'wide',
      options: {
        loginSuccessCallback(data) {
        }
      }
    })
  },
  mounted() {
    const customerService = {
      email: 'cs@company.com',
      username: 'Customer Service',
      password: 'password'
    }
    qiscus.setUser(customerService.email, customerService.password, customerService.username)
    QiscusSDK.render()
  },
  watch: {
    guests: function (newGuests) {
      const activeGuest = newGuests.find(it => it.active)
      qiscus.chatTarget(activeGuest.email)
    }
  },
  methods: {
    onChatTarget(target) {
      const guests = this.guests.map((guest) => {
        if (guest.id === target.id) guest.active = true
        else guest.active = false
        return guest
      })
      this.guests = guests
    },
    toggleWidget() {
      this.widgetHidden = !this.widgetHidden
    }
  }
}
</script>



